import React, { useState } from 'react'
import { Box, IconButton } from 'theme-ui'
import { FaTimes } from 'react-icons/fa'
import styles from '@widgets/Search/Search.styles'

import { useFlexSearch } from 'react-use-flexsearch'
import { useStaticQuery, graphql } from "gatsby"
import Card from '@components/Card'
import useDebounce from '@components/useDebounce'
import SearchInput from '@widgets/Search/Search.Input'

import useScrollDisabler from '@components/useScrollDisabler'

const Overlay = ({ onClick }) => (
  <>
    <Box sx={styles.overlay} onClick={onClick} />
    <Box sx={styles.close}>
      <IconButton onClick={onClick}>
        <FaTimes />
      </IconButton>
      <Box sx={styles.esc}>ESC</Box>
    </Box>
  </>
)

const Results = ({ results }) => {
  useScrollDisabler()
  return (
    <Box sx={styles.resultsWrapper}>
      <Box sx={styles.hitsWrapper}>
        <Box
          variant='lists.cards.fixed.search'
          sx={styles.hitGroup}
        >
          {results.map(result => {
            const node = {
              ...result,
              key: result.id,
              excerpt: result.description || "This post has no description, the EPI2ME Labs bot is very sad about this."
            }
            return (
              <Card
               variant='search'
               {...node}
               omitCategory
               omitFooter
               omitMedia
             />
            )
          })}
        </Box>
      </Box>
    </Box>
  )
}

const SearchBox = ({
  searchTerm,
  setSearchTerm,
  delay,
  focus,
  handleFocus,
  handleClose,
  ...rest
}) => {

  const handleEsc = e => {
    //close on esc keypress
    if (e.keyCode === 27) {
      e.currentTarget.blur()
      handleClose()
    }
  }
  return (
    <>
      <SearchInput
        focus={focus}
        onFocus={handleFocus}
        onChange={e => setSearchTerm(e.target.value)}
        onKeyDown={handleEsc}
        isLoaded
        {...rest}
      />
    </>
  )
}

const Search = ({ isFocused = false }) => {
  const [focus, setFocus] = useState(isFocused)
  // Set up search
  const queryData = useStaticQuery(graphql`
    query {
      localSearchPages {
        index
        store
      }
    }
  `)
  const index = queryData.localSearchPages.index
  const store = queryData.localSearchPages.store
  const [query, setQuery] = useState('')
  const debouncedQuery = useDebounce(query, 500)
  const results = useFlexSearch(query, index, store)

  const handleClose = () => setFocus(false)
  const handleFocus = () => !focus && setFocus(true)

  return (
    <Box>
      <Box>
        <SearchBox
          searchTerm={debouncedQuery}
          setSearchTerm={setQuery}
          focus={focus}
          handleFocus={handleFocus}
          handleClose={handleClose}
        />
        {focus && <Results results={results} />}
      </Box>
      {focus && (
        <>
          <Overlay onClick={handleClose} />
        </>
      )}
    </Box> 
  )
}

export default Search
